* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}
.flatpickr-input {
  padding: 4px 4px;
  background-color: inherit;
  border: none;
  border-bottom: 1px solid rgb(0, 132, 255);
  border-radius: 4px;
  width: 300px;
  font-size: 18px;
  margin-right: 20px;
}
button[data-start],
button[data-stop] {
  padding: 5px 20px;
  background-color: inherit;
  border: none;
  border-radius: 2px;
  box-shadow: 4px 0 4px 0 rgba(0, 0, 0, 0.2);
  color: rgb(0, 132, 255);
  font-size: 18px;
}
.timer {
  display: flex;
  margin-top: 30px;
}
.field {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.field:not(:last-child) {
  margin-right: 30px;
}
.field .value {
  color: rgb(0, 132, 255);
  font-size: 50px;
}
